@import url("https://www.w3schools.com/w3css/4/w3.css");

#dialog-section {
  position: relative;
}

#add-dialog, #edit-dialog, #delete-dialog {
  display: block;
}
#h3 {
    font-size: 30px;
}
#add-property-form img, #edit-property-form img{
    height: auto;
    width: 200px;
}
#add-property-form, #edit-property-form {
    background-color: green;
}
#delete-content {
  margin-bottom: 10px;
  background-color: green;
}

#img-prev {
  max-width: 100%;
}

#delete-dialog button {
  margin-right: 10px;
}

@media only screen and (min-width: 600px) {
  #img-prev-section {
    flex: 1;
  }

  #img-prev {
    max-width: 100%;
  }

  #img-upload {
    flex: 6;
  }
}